import React from 'react';

import { SetSchedule, AppointmentItem } from 'components/screens/user';
import { ScheduleData } from 'types/appointments';
import { convertAppointmentScheduleTime } from 'utilities';
import { SUCCESS, useApi } from 'store/callApi';
import config from 'config';
import { APPOINTMENT_REQUEST_TYPES, APPOINTMENT_REQUEST_TYPE_VALUES } from 'configs/constants';

const { REQUESTED } = APPOINTMENT_REQUEST_TYPES;

interface PropTypes {
  requestType: string;
  appointment: any;
  onConfirm: () => void;
}

const Edit = (props: PropTypes) => {
  const { callApi } = useApi();

  const { requestType, appointment, onConfirm } = props;
  const { id: appointmentId, appointment_status } = appointment || {};

  /**
   * Handle confirm
   *
   * @param data Schedule data
   */
  const handleConfirm = (scheduleDdata: ScheduleData) => {
    const scheduleTime = convertAppointmentScheduleTime(scheduleDdata);
    const { appointment_time_begin, appointment_time_end } = scheduleTime;
    const timeEdit =
      appointment_status === APPOINTMENT_REQUEST_TYPE_VALUES[REQUESTED]
        ? scheduleTime
        : {
            appointment_time_begin_want_edit: appointment_time_begin,
            appointment_time_end_want_edit: appointment_time_end,
          };

    callApi(
      {
        method: 'put',
        api: config.rest.updateDeleteAppointment(),
        body: {
          appointment_id: appointmentId,
          update_status: appointment_status,
          ...timeEdit,
        },
        loading: true,
      },
      (response: any) => {
        const { status } = response;
        if (status === SUCCESS) {
          onConfirm();
        }
      },
    );
  };

  return (
    <div className="Edit-area">
      <div className="Edit-info">
        <AppointmentItem requestType={requestType} appointment={appointment} isDetail />
      </div>
      <div className="Edit-content">
        <SetSchedule
          className="Edit-schedule"
          isEditSchedule
          lbDate="Neues Datum"
          lbTime="Neue Uhrzeit"
          onConfirm={handleConfirm}
        />
      </div>
    </div>
  );
};

export default Edit;
