import React, { useState, useEffect } from 'react';
import { isNil } from 'lodash';

import { InputTextArea } from 'components';
import Dialog, { DialogPropTypes } from './Dialog';

interface PropTypes extends DialogPropTypes {
  maxLength?: number;
}

const DialogTextArea = (props: PropTypes) => {
  const { open, maxLength, onConfirm = () => {}, ...rest } = props;
  const [value, setValue] = useState('');

  useEffect(() => {
    return () => {
      if (!open) setValue('');
    };
  }, [open]);

  /**
   * Handle change input
   *
   * @param e React ChangeEvent
   */
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { value = '' } = e.target;

    if (isNil(maxLength) || value.length <= maxLength) {
      setValue(value);
    }
  };

  /**
   * Handle close dialog
   */
  const handleConfirm = () => {
    onConfirm(value);
  };

  return (
    <Dialog open={open} onConfirm={handleConfirm} {...rest}>
      <InputTextArea className="Dialog-textArea" value={value} maxLength={maxLength} onChange={handleChange} />
    </Dialog>
  );
};

export default DialogTextArea;
