import React from 'react';

import { HBButtonSmall } from 'hbBaseClass';

import './Layout.scss';

interface PropTypes {
  className?: string | boolean;
  title?: string;
  btnBack?: string;
  onBack?: (param: any) => void;
  btnFilter?: string;
  btnFilterIcon?: string | boolean;
  onFilter?: () => void;
  btnConfirm?: string;
  btnConfirmDisabled?: boolean;
  onConfirm?: () => void;
  progressStep?: number;
  noBackground?: boolean;
  children?: React.ReactNode;
}

const AllAppointmentsLayout = (props: PropTypes) => {
  // props
  const {
    className = '',
    title = '',
    noBackground = false,
    btnBack = 'Zurück',
    btnFilterIcon,
    btnFilter = 'Filtern',
    onBack = () => {},
    onFilter,
    children,
  } = props;

  return (
    <div className={`hb-mx-20 AllAppointmentsLayout-area ${className}`}>
      <div className="AllAppointmentsLayout-header">
        {btnBack && (
          <HBButtonSmall color="petrol" onClick={onBack}>
            <i className="hb-icon-arrow-left" /> {btnBack}
          </HBButtonSmall>
        )}
        {btnFilter && (
          <HBButtonSmall className="btnFilter" color="petrol" onClick={() => (onFilter ? onFilter() : () => {})}>
            {btnFilter} {btnFilterIcon && <i className={`hb-icon-${btnFilterIcon}`} />}
          </HBButtonSmall>
        )}
      </div>
      <div className="AllAppointmentsLayout-wrapperContainer">
        {title && <h3 className="AllAppointmentsLayout-title">{title}</h3>}
        <div className={`AllAppointmentsLayout-container ${!noBackground && 'bg'}`}>{children}</div>
      </div>
    </div>
  );
};

export default AllAppointmentsLayout;
