import React from 'react';
import { useHistory } from 'react-router';
import { useTranslation } from 'react-i18next';

import { HBButton, HBButtonSmall } from 'hbBaseClass';
import { Frame } from 'components';
import { AppointmentItem } from 'components/screens/user';
import { loadingSmall } from 'extensions';
import { BUFFER_PAGE, MODE_OPTIONS } from './ViewAppointments';

import { APPOINTMENT_REQUEST_TYPES } from 'configs/constants';

const { REQUESTED, ACCEPTED, FINISHED, CANCELLED } = APPOINTMENT_REQUEST_TYPES;
const DESCRIPTIONS = {
  [REQUESTED]: 'Hier finden Sie alle Beratungen die sie beantragt haben.',
  [ACCEPTED]: 'Hier finden Sie alle Beratungen die von unseren Pflegeberatern angenommen wurden.',
  [FINISHED]: 'Hier finden Sie alle Beratungen die Sie durchgeführt haben.',
  [CANCELLED]: 'Hier finden Sie alle Beratungen die storniert wurden.',
};

const NO_RESULTS = {
  [REQUESTED]: 'Sie haben noch keine Beratung gebucht.',
  [ACCEPTED]: 'Sie haben noch keine akzeptierten Beratungen.',
  [FINISHED]: 'Sie haben noch keine durchgeführten Beratungen.',
  [CANCELLED]: 'Sie haben keine stornierten Beratungen.',
};

interface PropTypes {
  appointments: any[];
  requestType: string;
  isLoading?: boolean;
  currentPage: number;
  totalRecords: number;
  setMode: (mode: number) => void;
  setSelectedAppointment: (selectedAppointment: any) => void;
  setIsOpenPopupCancel: (isOpen: boolean) => void;
  handleLoadmore: () => void;
}

const ListAppointments = (props: PropTypes) => {
  const history = useHistory();
  const { t } = useTranslation();
  const {
    currentPage,
    totalRecords,
    isLoading,
    appointments = [],
    requestType,
    setMode,
    setSelectedAppointment,
    setIsOpenPopupCancel,
    handleLoadmore,
  } = props;

  return (
    <>
      {!!appointments?.length && <p className="AllAppointmentsLayout-desc">{DESCRIPTIONS[requestType]}</p>}
      <div className="AllAppointmentsLayout-list">
        {appointments?.length ? (
          appointments.map((appointment: any, index: number) => (
            <AppointmentItem
              key={index}
              requestType={requestType}
              appointment={appointment}
              onOpenPopupEdit={() => setMode(MODE_OPTIONS.EDIT)}
              onOpenPopupCancel={() => setIsOpenPopupCancel(true)}
              onSetSelectedAppointment={setSelectedAppointment}
              onClick={() => history.push(`/consulting/user/view-appointments/${requestType}/${appointment.id}`)}
            />
          ))
        ) : (
          <>
            {!isLoading && (
              <div className="AllAppointmentsLayout-noResults">
                <p>{NO_RESULTS[requestType]}</p>
                {requestType === REQUESTED && (
                  <div className="AllAppointmentsLayout-noResults_footer">
                    <HBButtonSmall onClick={() => history.push('/consulting/book-an-appointment')}>
                      Beratung buchen
                    </HBButtonSmall>
                  </div>
                )}
              </div>
            )}
          </>
        )}
      </div>
      <div className="AllAppointmentsLayout-loadmore">
        {isLoading && currentPage > BUFFER_PAGE && (
          <Frame className="AllAppointmentsLayout-loading" size="lg" render={loadingSmall} />
        )}
        {!isLoading && appointments?.length < totalRecords && (
          <HBButton onClick={handleLoadmore}>{t('loadMore')}</HBButton>
        )}
      </div>
    </>
  );
};

export default ListAppointments;
