import React, { useMemo, useState } from 'react';
import moment from 'moment';

import { HBButtonSmall } from 'hbBaseClass';
import { FormGroupSelect, FormGroupDatePicker, ButtonOptions } from 'components';
import { SCHEDULE_OPTIONS, MAX_SCHEDULE_TIME_START, MAX_SCHEDULE_TIME } from 'configs/constants';
import { getScheduleMinTimeEnd, getScheduleTimeOptions, getDefaultTimeEnd } from 'utilities';
import { TimeFilter } from 'types/appointments';
import { DEFAULT_FILTER } from './ViewAppointments';

const { TODAY, OTHER } = SCHEDULE_OPTIONS;
const dateOptions = [TODAY, OTHER].map((label: string) => ({ label, value: label }));
const defaultSpecificDate = new Date(moment().add(1, 'd').format('YYYY-MM-DD'));
const maxSpecificDate = new Date(moment().add(21, 'd').format('YYYY-MM-DD'));
const timeStartOptions = getScheduleTimeOptions('00:00', { maxTime: MAX_SCHEDULE_TIME_START });

interface PropTypes {
  timeFilter?: TimeFilter;
  onFilter: (timeFilter: TimeFilter) => void;
}

const Filter = (props: PropTypes) => {
  const { timeFilter, onFilter = () => {} } = props;
  const {
    selectedDate: filterSelectedOption = '',
    timeStart: filterTimeStart,
    timeEnd: filterTimeEnd,
  } = timeFilter || {};

  // states
  const isOther = filterSelectedOption && filterSelectedOption !== TODAY;
  const [selectedOption, setSelectedOption] = useState(isOther ? OTHER : filterSelectedOption);
  const [specificDate, setSpecificDate] = useState(isOther ? new Date(filterSelectedOption) : defaultSpecificDate);
  const [timeStart, setTimeStart] = useState(filterTimeStart || '00:00');
  const [timeEnd, setTimeEnd] = useState(filterTimeEnd || MAX_SCHEDULE_TIME);

  // Generate time end options
  const timeEndOptions = useMemo(() => getScheduleTimeOptions(getScheduleMinTimeEnd(timeStart, 0.25)), [timeStart]);

  // Convert specific date value
  const specificDateValue = useMemo(() => moment(specificDate).format('DD.MM.YYYY'), [specificDate]);

  /**
   * Handle change specific date
   *
   * @param value Specific date
   */
  const handleChangeSpecificDate = (value: Date) => {
    setSpecificDate(value);
  };

  /**
   * Handle change time
   *
   * @param isSetTimeStart Set time start or time end
   * @param e DOM Event
   */
  const handleChangeTime = (isSetTimeStart?: boolean) => (e: React.ChangeEvent<HTMLSelectElement>) => {
    const { value = '' } = e.target;

    if (isSetTimeStart) {
      setTimeStart(value);
      setTimeEnd(getDefaultTimeEnd(value));
      return;
    }

    setTimeEnd(value);
  };

  /**
   * Handle filter
   */
  const handleFilter = () => {
    const timeFilter = {
      selectedDate: selectedOption === OTHER ? moment(specificDate).format('YYYY-MM-DD') : selectedOption,
      timeStart,
      timeEnd,
    };

    onFilter(timeFilter);
  };

  /**
   * Handle reset filter
   */
  const handleResetFilter = () => {
    setSelectedOption('');
    setTimeStart('00:00');
    setTimeEnd(MAX_SCHEDULE_TIME);

    onFilter(DEFAULT_FILTER);
  };

  return (
    <div className="Filter-area">
      <div className="Filter-container">
        <div className="Filter-chooseDate">
          <div className="Filter-listDates">
            <h5 className="Filter-lb">Datum</h5>
            <ButtonOptions
              className="Filter-options"
              value={selectedOption}
              options={dateOptions}
              onClick={setSelectedOption}
            />
          </div>
          {selectedOption === OTHER && (
            <div className="Filter-datePicker">
              <h5 className="Filter-lb small">Datum wählen</h5>
              <FormGroupDatePicker
                disabled
                showYearDropdown
                showMonthDropdown
                placeholder="TT.MM.JJJJ"
                selected={specificDate}
                value={specificDateValue}
                maxDate={maxSpecificDate}
                onChange={handleChangeSpecificDate}
              />
            </div>
          )}
        </div>
        <div className="Filter-times">
          <h5 className="Filter-lb">Von – Bis</h5>
          <div className="Filter-timesOptions">
            <FormGroupSelect
              value={timeStart}
              options={timeStartOptions}
              disabled={!selectedOption}
              onChange={handleChangeTime(true)}
            />
            <span>und</span>
            <FormGroupSelect
              value={timeEnd}
              options={timeEndOptions}
              disabled={!selectedOption}
              onChange={handleChangeTime()}
            />
          </div>
        </div>
        <div className="Filter-actions">
          <HBButtonSmall disabled={!selectedOption} onClick={handleFilter}>
            Filter anwenden
          </HBButtonSmall>
          <span onClick={handleResetFilter}>Filter zurücksetzen</span>
        </div>
      </div>
    </div>
  );
};

export default Filter;
