import React from 'react';

import { HBModal, HBButtonSmall } from 'hbBaseClass';

import './Dialog.scss';

export interface DialogPropTypes {
  className?: string;
  open: boolean;
  title: string;
  hasClose?: boolean;
  description?: string;
  btnConfirm?: string;
  btnCancel?: string;
  children?: React.ReactNode;
  onConfirm?: (param?: any) => void;
  onCancel?: () => void;
  onClose?: () => void;
}

const Dialog = (props: DialogPropTypes) => {
  const {
    className = '',
    open,
    title = '',
    description = '',
    hasClose,
    btnConfirm,
    btnCancel,
    onConfirm = () => {},
    onCancel = () => {},
    onClose = () => {},
    children,
  } = props;

  return (
    <HBModal
      className={`Dialog-container ${className}`}
      centered
      show={open}
      closeBtn={hasClose}
      onClose={onClose}
      closeOnBackdrop={false}
    >
      <div className="Dialog-body">
        <h3 className="Dialog-title">{title}</h3>
        {description && <p className="Dialog-desc">{description}</p>}
        {children}
      </div>
      {(btnConfirm || btnCancel) && (
        <div className="Dialog-actions">
          {btnConfirm && <HBButtonSmall onClick={onConfirm}>{btnConfirm}</HBButtonSmall>}
          {btnCancel && (
            <span className={btnConfirm && 'margin'} onClick={onCancel}>
              {btnCancel}
            </span>
          )}
        </div>
      )}
    </HBModal>
  );
};

export default Dialog;
