import React, { useEffect, useMemo, useState, useCallback } from 'react';
import { RouteComponentProps } from 'react-router';
import { useHistory, useParams } from 'react-router-dom';
import { includes, isEqual } from 'lodash';
import moment from 'moment';

import { DialogTextArea } from 'components/dialog';
import AllAppointmentsLayout from './Layout';
import ListAppointments from './ListAppointments';
import Filter from './Filter';
import Edit from './Edit';

import { SUCCESS, useApi } from 'store/callApi';
import {
  APPOINTMENT_REQUEST_TYPES,
  APPOINTMENT_REQUEST_TYPE_VALUES,
  APPOINTMENT_TYPES,
  APPOINTMENT_TYPE_VALUES,
  DEFAULT_PAGE_SIZE,
  MAX_SCHEDULE_TIME,
  SCHEDULE_OPTIONS,
} from 'configs/constants';
import config from 'config';
import { TimeFilter } from 'types/appointments';

const { REQUESTED, ACCEPTED, FINISHED, CANCELLED } = APPOINTMENT_REQUEST_TYPES;

export const DEFAULT_FILTER = { selectedDate: '', timeStart: '00:00', timeEnd: MAX_SCHEDULE_TIME };
export const TITLES = {
  [REQUESTED]: 'Angefragt',
  [ACCEPTED]: 'Akzeptiert',
  [FINISHED]: 'Durchgeführt',
  [CANCELLED]: 'Storniert',
};
export const MODE_OPTIONS = { VIEW: 1, FILTER: 2, EDIT: 3 };
export const BUFFER_PAGE = 2;

const defaultSelectedAppointment = { id: '' };
// mode defined
const { VIEW, FILTER, EDIT } = MODE_OPTIONS;
const LAYOUT_TITLES = {
  [FILTER]: 'Filter',
  [EDIT]: 'Ausgewählte Beratung',
};

const ViewAppointments: React.FC<RouteComponentProps> = () => {
  const history = useHistory();
  const { appointmentType, requestType } = useParams<{ appointmentType: string; requestType: string }>();
  const { callApi } = useApi();

  // states
  const [mode, setMode] = useState(VIEW);
  const [timeFilter, setTimeFilter] = useState(DEFAULT_FILTER);
  const [appointmentsData, setAppointmentsData] = useState<any>({ appointments: [], totalRecords: 0 });
  const [isLoading, setIsLoading] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);

  // state selected appointment
  const [selectedAppointment, setSelectedAppointment] = useState(defaultSelectedAppointment);
  // state popup cancel
  const [isOpenPopupCancel, setIsOpenPopupCancel] = useState(false);

  const { appointments = [], totalRecords = 0 } = appointmentsData;

  // check filter or not
  const isFiltered = useMemo(() => !isEqual(timeFilter, DEFAULT_FILTER), [timeFilter]);

  // get layout title
  const layoutTitle = useMemo(() => LAYOUT_TITLES[mode] || TITLES[requestType], [mode, requestType]);

  // get button filter title
  const btnFilterTitle = useMemo(() => {
    if ((!appointments?.length && !isFiltered) || mode === EDIT) return '';

    return mode === FILTER ? 'Schließen' : 'Filtern';
  }, [appointments, isFiltered, mode]);

  /**
   * Get appointments by request type
   *
   * @param page Page wanna fetch data
   */
  const getAppointments = useCallback(
    (page: number, queryTimeFilter?: any) => {
      // if the first load => load double page size
      const buffer = page === 1 ? BUFFER_PAGE : 1;
      const selectedTimeFilter = queryTimeFilter || timeFilter;
      let requestTimeFilter = { begin: -1, end: -1 };

      // convert time filter for request api
      if (!isEqual(selectedTimeFilter, DEFAULT_FILTER)) {
        const { selectedDate, timeStart, timeEnd } = selectedTimeFilter;
        const mmSelectedDate = selectedDate === SCHEDULE_OPTIONS.TODAY ? moment() : moment(selectedDate);
        const selectedDateYMD = mmSelectedDate.format('YYYY-MM-DD');

        requestTimeFilter = {
          begin: moment(`${selectedDateYMD} ${timeStart}`).utc().unix(),
          end: moment(`${selectedDateYMD} ${timeEnd}`).utc().unix(),
        };
      }

      setIsLoading(true);
      callApi(
        {
          api: config.rest.getAppointments(),
          method: 'post',
          body: {
            appointment_type: APPOINTMENT_TYPE_VALUES[appointmentType],
            appointment_status: APPOINTMENT_REQUEST_TYPE_VALUES[requestType],
            between: [requestTimeFilter],
            page,
            page_size: buffer * DEFAULT_PAGE_SIZE,
          },
          loading: page === 1,
        },
        (response: any) => {
          setIsLoading(false);
          const { status, data } = response;
          if (status !== SUCCESS) return;
          const { consulting_appointments = [], total_record } = data;

          setAppointmentsData((prevAppointmentsData: any) => {
            const { appointments: prevAppointments = [] } = prevAppointmentsData;

            return {
              appointments: page === 1 ? consulting_appointments : [...prevAppointments, ...consulting_appointments],
              totalRecords: total_record,
            };
          });
          if (page === 1) setCurrentPage(2);
        },
      );
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [appointmentType, requestType, timeFilter],
  );

  useEffect(() => {
    if (
      !includes(Object.values(APPOINTMENT_TYPES), appointmentType) ||
      !includes(Object.values(APPOINTMENT_REQUEST_TYPES), requestType)
    ) {
      history.push('/404');
      return;
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    getAppointments(1);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [requestType]);

  /**
   * Handle load more data
   */
  const handleLoadmore = () => {
    const nextPage = currentPage + 1;

    setCurrentPage(nextPage);
    getAppointments(nextPage);
  };

  /**
   * Handle cancel appointment
   *
   * @param reason Reason cancel
   */
  const handleCancelAppointment = (reason?: string) => {
    callApi(
      {
        method: 'delete',
        api: config.rest.updateDeleteAppointment(),
        body: {
          appointment_id: selectedAppointment.id,
          update_status: APPOINTMENT_REQUEST_TYPE_VALUES[CANCELLED],
          reason_refusal: reason || '',
        },
        loading: true,
      },
      (response) => {
        const { status } = response;

        setIsOpenPopupCancel(false);
        if (status === SUCCESS) {
          getAppointments(1, DEFAULT_FILTER);
          setMode(VIEW);
        }
      },
    );
  };

  /**
   * Handle close popup edit
   */
  const handleConfirmEdit = () => {
    setMode(VIEW);
    setSelectedAppointment(defaultSelectedAppointment);
    getAppointments(1, timeFilter);
  };

  /**
   * Handle back
   */
  const handleBack = () => {
    if (includes([FILTER, EDIT], mode)) {
      setMode(VIEW);
      return;
    }

    history.push(`/consulting/${appointmentType}/view-appointments`);
  };

  /**
   * Handle filter
   */
  const handleFilter = (timeFilter: TimeFilter) => {
    setMode(VIEW);
    setTimeFilter(timeFilter);

    getAppointments(1, timeFilter);
  };

  /**
   * Handle render children content
   */
  const renderChildren = () => {
    switch (mode) {
      case VIEW: {
        return (
          <ListAppointments
            appointments={appointments}
            currentPage={currentPage}
            totalRecords={totalRecords}
            isLoading={isLoading}
            requestType={requestType}
            setMode={setMode}
            setSelectedAppointment={setSelectedAppointment}
            setIsOpenPopupCancel={setIsOpenPopupCancel}
            handleLoadmore={handleLoadmore}
          />
        );
      }
      case FILTER:
        return <Filter timeFilter={timeFilter} onFilter={handleFilter} />;
      case EDIT:
        return <Edit requestType={requestType} appointment={selectedAppointment} onConfirm={handleConfirmEdit} />;
    }
  };

  return (
    <AllAppointmentsLayout
      className={isFiltered && 'filter'}
      title={layoutTitle}
      onBack={handleBack}
      btnFilterIcon={mode === FILTER && 'cross'}
      btnFilter={btnFilterTitle}
      onFilter={() => setMode(mode === FILTER ? VIEW : FILTER)}
      noBackground={mode === EDIT}
    >
      {renderChildren()}
      <DialogTextArea
        open={isOpenPopupCancel}
        maxLength={200}
        hasClose
        title="Assistenz stornieren"
        description="Bitte nennen Sie uns dne Grund warum Sie die Assitenz stornieren wollen"
        btnConfirm="Grund übermitteln"
        btnCancel="Stornierung abbrechen"
        onConfirm={handleCancelAppointment}
        onCancel={() => setIsOpenPopupCancel(false)}
        onClose={() => setIsOpenPopupCancel(false)}
      />
    </AllAppointmentsLayout>
  );
};

export default ViewAppointments;
